.App {
  text-align: center;
}

.logo {
  width: 400px;
  padding: 5px;
}

.App-header {
  background-color: rgb(184, 207, 221);
  min-height: 20vh;
  justify-content: center;
  display: flex;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
  border-style: solid;
  border-color: rgb(154, 177, 192);
  padding: 60px;
}

footer {
  background-color: rgb(184, 207, 221);
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  border-style: solid;
  border-color: rgb(154, 177, 192);
  padding: 30px;
}

.responsive {
  border-radius: 5px;
  border-style: solid;
  border-color: lightslategray;
  max-width: 100%;
  max-height: auto;
}

.responsive2 {
  border-radius: 5px;
  border-style: solid;
  border-color: rgb(180, 190, 199);
  max-width: 100px;
  max-height: 100px;
}

.responsive3 {
  border-radius: 5px;
  border-style: solid;
  border-color: rgb(130, 190, 199);
  width: 80px;
  height: 70px;
}

.Productinfo {
  background-color: rgb(255, 255, 255);
  width: 60%;
  margin: auto;
  padding: 10px;
}

.Shopinfo {
  height: auto;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(255, 255, 255);
}

.Shopinfo2 {
  justify-content: center;
  margin: auto;
  padding: 10px;
  width: 40%;
  height: auto;
  border-radius: 20px;
  background-color: #ffffff;
  box-shadow: 0px 3 5px 50px #1687d933;
}

.Items {
  margin: auto;
  background-color: rgb(255, 255, 255);
  width: 70%;
  padding: 10px;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

.App-link {
  color: #61dafb;
}

button {
  font-size: large;
  background-color: rgb(37, 178, 159);
  border-radius: 5px;
  cursor: pointer;
  border-style: none;
  color: azure;
  padding: 10px;
}

input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type=email], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type=number], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.Contactinfo {
  background-color: #ffffff;
  text-align: left;
  width: 40%;
  height: 100%;
  margin: auto;
  padding: 20px;
  border-radius: 20px;
}

.Information2 {
  margin: auto;
  background-color: #ffffff;
  padding: 10px;
  border-radius: 20px;
  width: 50%;
}

.Contacts {
  margin: auto;
  width: 30%;
  text-align: left;
  padding: 50px;
  background-color: rgb(158, 220, 212);
}

.Contacts2 {
  margin: auto;
  display: flex;
  justify-content: space-between;
  text-align: center;
  padding: 50px;
  background-color: rgb(158, 220, 212);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.Shop {
  margin: auto;
  display: flex;
}

.continue {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}

.buttons {
  align-items: center;
  display: flex;
}

@media screen and (max-width: 600px) {
  body {
    max-width: auto;
  }
  .logo {
    width: 400px;
    padding: 5px;
  }
  .Contactinfo {
    width: 100%;
  }
}

@media screen and (max-width: 1000px) {
  .Shopinfo {
    width: auto;
    flex-direction: column;
    float: center;
    margin: 0%;
    margin-left: 0%;
    display: flex;
  }
  .Shopinfo2 {
    flex-direction: column;
    float: center;
    width: auto;
    margin: 0%;
    margin-left: 0%;
    display: flex;
  }
  .Items {
    width: auto;
    margin-left: 0%;
    height: auto;
    float: center;
  }
  .responsive {
    width: auto;
    height: auto;
  }
  .responsive2 {
    width: auto;
    height: auto;
  }
  .responsive3 {
    width: 20;
    height: 20;
  }
  .Contactinfo {
    background-color: #ffffff;
    text-align: left;
    width: auto;
    height: 100%;
    margin: auto;
    padding: 20px;
    border-radius: 20px;
  }
  
  .Information2 {
    margin: auto;
    background-color: #ffffff;
    padding: 10px;
    border-radius: 20px;
    width: auto;
  }
  
  .Contacts {
    margin: auto;
    width: auto;
    text-align: left;
    padding: 50px;
  }
  
  .Contacts2 {
    margin: auto;
    display: block;
    width: auto;
    justify-content: space-between;
    text-align: center;
    padding: 50px;
  }
  .Shop {
    display: block;
    margin-left: auto;
  }
  .App {
    text-align: center;
    display: block;
  }
  .footer {
    width: auto;
    height: auto;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
